n}\n\n.treats-list li{\n    display:inline-block;\n    font-family: loveloblack;\n    padding-right:12px;\n    font-size: 13px;\n}\n\n/************************/\n\n@font-face {\n    font-family: 'Material Icons';\n    font-style: normal;\n    font-weight: 400;\n    src: url('https://fonts.gstatic.com/s/materialicons/v38/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2') format('woff2');\n}\n\n.material-icons {\n    font-family: 'Material Icons';\n    font-weight: normal;\n    font-style: normal;\n    font-size: 24px;\n    line-height: 1;\n    letter-spacing: normal;\n    text-transform: none;\n    display: inline-block;\n    white-space: nowrap;\n    word-wrap: normal;\n    direction: ltr;\n    -webkit-font-feature-settings: 'liga';\n    -webkit-font-smoothing: antialiased;\n}\n\n/*****On Hover*******/\n\n/* .products-item:hover{\n\n}\n\n.received-candies, ul, .prod-available{\n    opacity:0;\n}\n.received-candies:hover, \nul:hover, \n.prod-available:hover{\n    opacity:1;\n} */\n\n.products-item{\n    box-shadow: none;\n}\n\n.products-item:hover{\n    -webkit-animation: boxShadow .5s;\n    animation:  boxShadow .51s;\n    box-shadow: 0 5px 15px rgba(0,0,0,0.15); \n}\n\n.products-item:hover .upper-cont{\n    opacity: 1;\n    -webkit-animation: fadeIn .5s;\n    animation: fadeIn .5s;\n}\n\n.products-item:hover .treats-list{\n    opacity: 1;\n    -webkit-animation: fadeIn .5s;\n    animation: fadeIn .5s;\n}\n\n.testClass{\n    color:#fff;\n}\n\n.treats-list{\n    opacity:0;\n}\n\n/*ANIMATION KEYFRAMES*/\n\n@-webkit-keyframes fadeIn {\n    from { opacity: 0; }\n    to { opacity: 1; }\n}\n\n@keyframes fadeIn {\n    from { opacity: 0; }\n    to { opacity: 1; }\n}\n\n@-webkit-keyframes boxShadow {    \n    0% { box-shadow: none }\n    100% { box-shadow: 0 5px 15px rgba(0,0,0,0.15); }\n}\n\n@keyframes boxShadow {\n    0% { box-shadow: none }\n    100% { box-shadow: 0 5px 15px rgba(0,0,0,0.15); }\n}  "